@media (min-width: 320px) {
  .name-input._error {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 18px;
      height: 3px;
      border-radius: 20%;
      background-color: #F97676;
      top: 23px;
      left: 257px;
      transform: rotate(45deg);
    }

    &::after {
      content: "";
      position: absolute;
      width: 18px;
      height: 3px;
      border-radius: 20%;
      background-color: #F97676;
      top: 23px;
      left: 257px;
      transform: rotate(-45deg);
    }
  }

  .name-input__input {
    width: 270px;
    border: none;
    height: 46px; 

    &::placeholder {
      font-family: "Ubuntu", sans-serif;
      font-size: 13px;
      color: #1C1C1C;
      opacity: 0.4;
    }
  }

  .name-input__input._error {
    outline: 2px solid #F97676;
  }

  .name-input__input {
    &:focus {
      outline: 2px solid #0DD1A2;
    }

    &:active {
      outline: 2px solid #0DD1A2;
    }

    // &:valid {
    //   outline: 2px solid #1DC04B;
    //   position: relative;
    // }
  }

